
/**
 *  mapaddOverlay
 * +60
 * @param {Object} lng 
 * @param {Object} lat 
 * @param {Object} sDegree ()
 *
 */
let map;

export function drawSectorOmitAngle(lng, lat, sDegree,baiduMap,angle) {
    map = baiduMap;
    var radius = 100; //
    //90
    sDegree = sDegree - 90;
    let eDegree = sDegree + angle;
    var color = "blue";
    return drawSector(lng, lat, radius, sDegree, eDegree, color);
}


export function drawSectorOmit(lng, lat, sDegree,baiduMap) {
    return drawSectorOmitAngle(lng, lat, sDegree,baiduMap,60);
}

/**
 *  mapaddOverlay
 * @param {Object} lng 
 * @param {Object} lat 
 * @param {Object} radius 
 * @param {Object} sDegree ()
 * @param {Object} eDegree 
 * @param {Object} color 
 *
 */
export function drawSector(lng, lat, radius, sDegree, eDegree, color) {
    if (eDegree < sDegree) {
        console.log("drawSector() ----> ");
        return "-1";
    }

    var point = new BMap.Point(lng, lat);

    var strokeColor = color; //
    var strokeWeight = 1; //
    var strokeOpacity = 0.8; //0 - 1
    var strokeStyle = 'solid'; //soliddashed
    var fillColour = color; //
    var fillOpacity = 0.4; //


    var points = []; //
    var step = ((eDegree - sDegree) / 1000) || 1000; //1000

    points.push(point); //

    //
    for (var i = sDegree; i < eDegree + 0.001; i += step) {
        points.push(EOffsetBearing(point, radius, i));
    }

    points.push(point); //

    //
    var polygon = new BMap.Polygon(
        points, {
            strokeColor: strokeColor,
            strokeWeight: strokeWeight,
            strokeOpacity: strokeOpacity,
            strokeStyle: strokeStyle,
            fillColor: fillColour,
            fillOpacity: fillOpacity
        });

    return polygon;
}

//
//point:; dist:; angle: 180-180 
export function EOffsetBearing(point, dist, angle) {
    var lngConv = map.getDistance(point, new BMap.Point(point.lng + 0.1, point.lat)) * 10; //1
    var latConv = map.getDistance(point, new BMap.Point(point.lng, point.lat + 0.1)) * 10; //1
    var lat = dist * Math.sin(angle * Math.PI / 180) / latConv; //
    var lng = dist * Math.cos(angle * Math.PI / 180) / lngConv; //
    return new BMap.Point(point.lng + lng, point.lat + lat);
}

export function MP(ak) {
    return new Promise(function (resolve, reject) {
        window.onload = function () {
            resolve(BMap)
        };
        var script = document.createElement("script");
        script.type = "text/javascript";
        script.src = "http://api.map.baidu.com/api?v=2.0&ak="+ak+"&callback=init";
        script.onerror = reject;
        document.head.appendChild(script);
    })
}
